
import Vue from "vue";
import Loader from "./Loader.vue";

export default Vue.extend({
  name: "Picture",
  components: {
    Loader,
  },
  props: {
    imgSrc: String,
  },
  data() {
    return {
      isLoaded: false,
    };
  },
  methods: {
    onImgLoad() {
      this.isLoaded = true;
    },
  },
});
